@font-face {
  font-family: 'ZangeziSans';
  src: url('../assets/fonts/ZangeziSans.woff2') format('woff2'),
      url('../assets/fonts/ZangeziSans.woff') format('woff');
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  font-feature-settings: 'zero' on, 'case' on, 'liga' off;
}

@font-face {
  font-family: 'ZangeziSans-Text';
  src: url('../assets/fonts/ZangeziSans-Text.woff2') format('woff2'),
      url('../assets/fonts/ZangeziSans-Text.woff') format('woff');
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  font-feature-settings: 'zero' on, 'case' on, 'liga' off;
}

$primaryFont: 'ZangeziSans', sans-serif;

$secondFont: 'ZangeziSans-Text', sans-serif;